'use strict';

const htmlForm = `
<form class = "new-employee-form">
<label>
  Name: 
  <input 
    class = "input"
    name="name" 
    type="text" 
    data-qa="name"
    minlength="4"
    required
  >
</label>
<label>
  Position: 
  <input 
    class="input"
    name="position" 
    type="text" 
    data-qa="position" 
    required
  >
</label>
<label>
  Office: 
  <select 
    class="input"
    name="office" 
    type="text" 
    data-qa="office"
  >
    <option>Tokyo</option>
    <option>Singapore</option>
    <option>London</option>
    <option>New York</option>
    <option>Edinburgh</option>
    <option>San Francisco</option>
  </select>
</label>
<label>
  Age: 
  <input 
    class = "input"
    name="age" 
    type="number" 
    data-qa="age"  
    min="18" 
    max="90" 
    required
  >
</label>
<label>
  Salary: 
  <input 
    class = "input"
    name="salary" 
    type="number" 
    data-qa="salary" 
    required
  >
</label>
<button class = "form-button">Save to table</button>
</form>`;

document.body.insertAdjacentHTML('beforeend', htmlForm);
